@import url(https://fonts.googleapis.com/css?family=Mrs+Saint+Delafield&display=swap);
@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.toggle-button {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 25px;
    width: 32px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
}

.toggle-button:focus {
    outline: none;
}

.toggle-button-line {
    width: 30px;
    height: 2px;
    background: #FFFFFF;

}
.toolbar {
    position: fixed;
    width: 100%;
    background: rgb(8, 12, 41);
    height: 60px;
    z-index: 9999;
    box-shadow: 0 8px 6px -6px rgb(0, 0, 0);
}

.toolbar-navigation {
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0 1rem;
}

.toolbar-logo {
    margin-left: 1rem;
}

.blinking-cursor {
    -webkit-animation: blinkingText 1.1s infinite;
            animation: blinkingText 1.1s infinite;
    font-size: 2.1rem;
}

@-webkit-keyframes blinkingText {
    0%{ color: #FFFFFF; }
    49%{ color: #FFFFFF; }
    50%{ color: transparent; }
    99%{ color: #2E3132; }
    100%{ color: #2E3132; }
}

@keyframes blinkingText {
    0%{ color: #FFFFFF; }
    49%{ color: #FFFFFF; }
    50%{ color: transparent; }
    99%{ color: #2E3132; }
    100%{ color: #2E3132; }
}

.toolbar-logo a {
    text-decoration: none;
    font-size: 2rem;
}

.spacer {
    flex: 1 1;
}

.toolbar-navigation-items ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
}

.toolbar-navigation-items li {
    padding: 0.75rem;
}

.toolbar-navigation-items a {
    text-decoration: none;
}

.toolbar-navigation-items a:hover,
.toolbar-navigation-items a:active {
    color: rgb(255, 251, 0);
    text-decoration: none;
}

.white {
    color: #FFFFFF;
}

.yellow {
    color: rgb(255, 251, 0);
}

@media (max-width: 768px) {
    .toolbar-navigation-items {
        display: none;
    }
}

@media (min-width: 769px) {
    .toolbar-toggle-button {
        display: none; 
    }

    .toolbar-logo {
        margin-left: 0;
    }
}
.side-drawer {
    height: 100%;
    background: #042E46;
    box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    width: 70%;
    max-width: 400px;
    z-index: 200;
    transform: translateX(-100%);
    transition: transform 0.4s;
}

.side-drawer.open {
    transform: translateX(0);
}

.acm-image {
    width: 100%;
    position: relative;
    top: 60px;
}

.side-drawer ul {
    position: relative;
    top: 60px;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.side-drawer li {
    margin: 0.5rem 0;
}

.side-drawer a {
    color: #FFFFFF;
    text-decoration: none;
    font-size: 1.2rem;
}

.side-drawer a:hover,
.side-drawer a:active {
    color: rgb(255, 251, 0);
}
.backdrop {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0,0,0,0.3);
    z-index: 100;
}
.banner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-image: url(/acm-sdstate/static/media/banner.b816d253.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    max-width: 100%;
}

.center {
    text-align: center;
}

.shadow {
    text-shadow: 2px 2px black;
}

.banner-quote {
    color: rgb(246, 246, 92);
}

.banner-signature {
    color: rgb(246, 246, 92);
    font-family: 'Mrs Saint Delafield', cursive;
    font-size: 50px;
}

.scroll, .scroll:hover {
    text-decoration: none;
    color: #FFFFFF;
    position: absolute;
    left: 50%;
    margin-left: -2em;
    bottom: 0;
    height: 70px;
    background-image: url(/acm-sdstate/static/media/arrow.977a7a0a.svg);
    background-position: bottom;
    background-repeat: no-repeat;
}
.container {
    color: rgb(8, 12, 41);
}

.events {
    display: flex;
    flex-direction: column;
}

.events .card {
    margin: 15px 0px;
    background-color: #042E46;
    color: white;
}

